.find-friend {
    .header-search-people {
      padding-bottom: 12px;
      .form-input-search {
        padding: 10px 10px 0 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        input{
          border-style: none;
          outline-style: none;
          padding-left: 10px;
        }
        label {
          border-bottom: 1px solid #aaa;
          border-bottom-left-radius: 10px;
          margin-right: 5px;
          padding: 0 8px 0 4px;
          flex: .5;
          display: flex;
          align-items: center;
          
        }
        button {
          padding: 2px;
          background: transparent;
          border: 1px solid #aaa;
          outline-style: none;
          cursor: pointer;
          font-weight: bold;
          color: rgb(58, 120, 255);
          border-radius: 20px;
        }
        button:hover {
          border-color: transparent;
        }
      }
    }
    .result-search-people {
      margin-top: 40px;
      .list-people {
        li {
          display: flex;
          height: 35px;
          margin: 4px 0;
          align-items: center;
          .list-people-avatar {
              flex: .1;
          }
          .list-people-user-info {
            display: flex;
            justify-content: space-around;
            flex: .8;
            h5 {
              flex:5;
            }
            button {
              flex: 1;
              border-style: none;
              outline-style: none;
              background: transparent;
              cursor: pointer;
            }
          }
        }
      }
  
    }
  }